@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

*,
*::after,
*::before {
    box-sizing: border-box;
}

:root {
    font-size: 20px;
}

::selection {
    background: #111628;
    color: white;
}

html,
body {
    margin: 0;
    padding: 0;
    background: #111628;
    overflow: hidden;
    font-family: 'Roboto', sans-serif;
    color: #8b8b8b;
    font-size: 0.9rem;
}

a {
    color: #727272;
    text-decoration: none;
}

a:focus,
a:hover {
    color: #8b8b8b;
}

#root {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.logo {
    width: 150px;
}

.scrollArea {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: auto;
}

.canvas > div {
    z-index: 1;
}

.frame {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    padding: 1.5rem;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.frame__title {
    font-size: inherit;
    font-weight: normal;
    margin: 0;
}

.frame__nav {
    display: none;
}

.frame__links {
    margin: 1rem 0;
}

.frame__link:not(:last-child) {
    margin-right: 0.5rem;
}

h3 {
    font-size: 2vh;
}

.bottom-left {
    padding: 1.5rem;
    transform: translate3d(0, -100%, 0) !important;
    width: 100vw;
}

@media screen and (min-width: 53em) {
    .frame {
        display: grid;
        height: 100vh;
        padding: 2rem 3.7rem;
        pointer-events: none;
        align-content: space-between;
        grid-template-columns: 50% 50%;
        grid-template-rows: auto auto auto;
        grid-template-areas:
          "title links"
          "... ..."
          "... nav";
    }
    .frame__title {
        grid-area: title;
    }
    .frame__links {
        grid-area: links;
        justify-self: end;
        margin: 0;
    }
    .frame__link {
        pointer-events: auto;
    }
    .frame__nav {
        display: block;
        grid-area: nav;
        justify-self: end;
    }
    .bottom-left {
        padding: 0 0 2rem 3.7rem;
        width: 30vw;
    }
}
